import React from 'react'

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';

import Icon24CameraOutline from '@vkontakte/icons/dist/24/camera_outline';
const APOD = ({id, this: props}) => (
    <Panel id={id}>
        <PanelHeader></PanelHeader>
        <div className="image_APOD">
            <img className="image_APOD" alt="" src="https://cdn.dribbble.com/users/14268/screenshots/4578050/spaceman.png"/>
        </div>
        <div className="info_APOD">
            <div className="title_APOD">{props.state.APOD['title']}</div>
            <div className="date_APOD">Copyright: {props.state.APOD['copyright']}</div>
            <div className="date_APOD">Date: {props.state.APOD['date']}</div>
            <div className="explanation_APOD">{props.state.APOD['explanation']}</div>
        </div>
        <Button onClick={() => props.watch_APOD()} className="button_APOD" before={<Icon24CameraOutline/>} size="xl" level="secondary">Watch</Button>
    </Panel>
);

export default APOD;