import React from "react";
import '@vkontakte/vkui/dist/vkui.css';
import connect from '@vkontakte/vkui-connect'
import "./style.css"
// Elements
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
// Panels
import Home from './panels/home'
import APOD from './panels/APOD'
import { Root, ConfigProvider } from "@vkontakte/vkui";

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			activeView: "home",
			popout: null,
			activeModal: null,
			modalHistory: [],
			APOD: []
			};
	}

	setActiveModal(activeModal) {
		activeModal = activeModal || null;
		let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];
	
		if (activeModal === null) {
		  modalHistory = [];
		} else if (modalHistory.indexOf(activeModal) !== -1) {
		  modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
		} else {
		  modalHistory.push(activeModal);
		}
	
		this.setState({
		  activeModal,
		  modalHistory
		});
	  };

	APOD() {
        this.setState({activeView: "apod", activePanel: "apod", popout: <ScreenSpinner/>})
		fetch("https://api.nasa.gov/planetary/apod?api_key=nzlgmMFP7cohVhvcrclOvCelQgg2AbUbqL275bft")
		.then(res => res.json())
		.then(result => {
			this.setState({popout: null, APOD: result})
		})
	}

	watch_APOD() {
		connect.send("VKWebAppShowImages", { 
			images: [this.state.APOD['url']]
		  });
	}

	render() {
		// const modal = (
		//    <ModalRoot activeModal={this.state.activeModal}>
		// 	<ModalPage
		// 	  id={MODAL_PAGE_FILTERS}
		// 	  onClose={this.modalBack}
		// 	  header={
		// 		<ModalPageHeader
		// 		  left={IS_PLATFORM_ANDROID && <HeaderButton onClick={this.modalBack}><Icon24Cancel /></HeaderButton>}
		// 		  right={<HeaderButton onClick={this.modalBack}>{IS_PLATFORM_IOS ? 'Готово' : <Icon24Done />}</HeaderButton>}
		// 		>
		// 		  Фильтры
		// 		</ModalPageHeader>
		// 	  }
		// 	>

		// 	  </ModalPage>
		//   </ModalRoot>
		// )
		return (
		<ConfigProvider scheme="bright_light">
		<Root activeView={this.state.activeView}>
				<View 
				id="home" 
				activePanel={this.state.activePanel} 
				popout={this.state.popout}
				>
				    <Home id="home" go={this.go} this={this}/>
				</View>
				<View 
				id="apod" 
				activePanel={this.state.activePanel} 
				popout={this.state.popout}
				>
				    <APOD id="apod" go={this.go} this={this}/>
				</View>
		</Root>
		</ConfigProvider>
			)
		  }
		}

export default App;
